* {
	margin: 0;
	padding: 0;
}

html {
	font-size: 62.5%;
	font-family: "Microsoft YaHei UI", "Microsoft YaHei UI Light";
}

body {
	font-size: 1em;
	width: 100%;
}

h1 {
	display: none;
}

a:link {
	text-decoration: none;
}

a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

a:active {
	text-decoration: none;
}

/******头部导航栏、工具栏区域START******/

#header {
	width: 100%;
	max-width: 1920px;
	min-width: 1200px;
}

.c-nav {
	width: 100%;
}

.c-nav a:link {
	color: #000;
}

.c-nav a:visited {
	color: #000;
}

.c-nav a:hover {
	color: #00a8ff;
}

.c-nav a:active {
	color: #00a8ff;
}

.c-nav-inner {
	width: 100%;
	font-size: 1.6em;
	background-color: #000000;
	height: 65px;
	color: #ffffff;
	position: relative;
}

a.c-nav-trigger {
	width: 80px;
	height: 100%;
	text-align: center;
	display: inline-block;
	margin-right: 20px;
}

a.c-nav-trigger i {
	display: inline-block;
	vertical-align: middle;
	width: 32px;
	height: 32px;
	text-indent: -10000px;
	background: url("../img/spriteSheet.png") no-repeat;
	background-position: -80px -40px;
}

.c-nav-logo {
	width: 145px;
	height: 100%;
	text-align: center;
	display: inline-block;
	margin-right: 20px;
}

.c-nav-logo i {
	display: inline-block;
	vertical-align: middle;
	width: 135px;
	height: 40px;
	text-indent: -10000px;
	background: url("../img/spriteSheet.png") no-repeat;
	background-position: -0px -0px;
}

ul.c-navTop-list {
	width: auto;
	height: 100%;
	display: inline-block;
	text-align: center;
	list-style: none;
}

ul.c-navTop-list li {
	display: inline-block;
	width: 90px;
	height: 65px;
	line-height: 65px;
	vertical-align: middle;
	cursor: pointer;
}

ul.c-navTop-list li:hover {
	color: #00a8ff;
}

.c-nav-trigger:after,
.c-nav-logo:after,
ul.c-navTop-list:after {
	content: '';
	display: inline-block;
	width: 0;
	height: 100%;
	vertical-align: middle;
}

.tool-right {
	width: auto;
	height: 65px;
	position: absolute;
	right: 20px;
	top: 0;
}

.c-sysName {
	width: auto;
	height: 100%;
	display: table;
	float: left;
	text-align: center;
}

.c-sysName strong {
	display: table-cell;
	font-size: 1.2em;
	width: 200px;
	vertical-align: middle;
}

.c-sysName:before {
	content: '';
	display: table-cell;
	vertical-align: middle;
	width: 21px;
	height: 65px;
	background: url("../img/spriteSheet.png") no-repeat;
	background-position: -135px -0px;
}

#siteTools {
	width: 300px;
	height: 100%;
	display: table;
	float: right;
	text-align: center;
}

#siteTools a {
	color: #ffffff;
	display: table-cell;
	width: 80px;
	height: 32px;
	vertical-align: middle;
}

#siteTools a:link {
	color: #ffffff;
}

#siteTools a:hover {
	color: #e60012;
}

#siteTools span {
	display: table-cell;
	width: 5px;
	height: 32px;
	vertical-align: middle;
}

/******头部导航栏、工具栏区域END******/

/******头部导航栏浮层START******/

.c-nav-wrapper {
	width: auto;
	max-width: 1200px;
	font-size: 1.4em;
	position: absolute;
	z-index: 9999;
	background-color: #efefef;
	left: 265px;
	-webkit-box-shadow: 0 0 30px #434343;
	box-shadow: 0 0 30px #434343;
}

.nav-inner-wrapper {
	width: auto;
	margin: 0 auto;
	position: relative;
	display: none;
}

.nav-inner-wrapper .wrapper-list {
	display: inline-block;
	vertical-align: top;
	margin: 20px;
	width: 200px;
}

.nav-inner-wrapper .wrapper-list h3 {
	width: 100%;
	font-size: 1.2em;
	height: 30px;
	line-height: 30px;
	border-bottom: 1px solid #dedede;
}

.nav-inner-wrapper .wrapper-list ul.media-list {
	width: 100%;
	list-style: none;
}

.nav-inner-wrapper .wrapper-list ul.media-list li {
	width: 100%;
	height: 30px;
	line-height: 30px;
}

.nav-inner-wrapper:before {
	content: '';
	display: block;
	width: 16px;
	height: 16px;
	position: absolute;
	top: -16px;
	background: url("../img/spriteSheet.png") no-repeat;
	background-position: -135px -65px;
}

.nav-inner-wrapper:after {
	content: '';
	display: block;
	clear: both;
}

.nav-inner-wrapper.adSourceWrapper:before {
	left: 45px;
}

.nav-inner-wrapper.adPrice:before {
	left: 135px;
}

.nav-inner-wrapper.paperManage:before {
	left: 225px;
}

.nav-inner-wrapper.niw-active {
	display: block;
}

/******头部导航栏浮层END******/

/******左侧全站导航-分级菜单START******/

#globalLeftNavBox {
	width: 230px;
	font-size: 1.4em;
	position: absolute;
	z-index: 10;
	top: 65px;
	left: -250px;
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
}

#globalLeftNavBox h3 {
	width: 220px;
	margin-left: 5px;
	height: 60px;
	line-height: 60px;
	border-bottom: 1px solid #dedede;
}

#globalLeftNavBox .other-enter-box {
	width: 220px;
	margin-left: 5px;
	margin-top: 20px;
	padding-top: 20px;
	padding-bottom: 20px;
	border-top: 1px solid #dedede;
	border-bottom: 1px solid #dedede;
}

#globalLeftNavBox .other-enter-box .enter {
	height: 30px;
	line-height: 30px;
}

#globalLeftNavBox .other-info {
	width: 220px;
	margin-left: 5px;
	padding-top: 20px;
	padding-bottom: 20px;
}

#globalLeftNavBox .other-info p {
	height: 30px;
	line-height: 30px;
}

#globalLeftNavBox .ui-widget.ui-widget-content {
	width: 230px;
	background: #efefef;
	min-height: 300px;
}

#globalLeftNavBox .my-icon-option {
	display: inline-block;
	position: relative;
	margin-right: 5px;
}

#globalLeftNavBox.slideOut {
	left: 0;
}

/******左侧全站导航-分级菜单END******/

/******全站通知START******/

.site-notice-box {
	width: 100%;
	position: relative;
	max-width: 1920px;
	min-width: 1200px;
	background-color: #00a8ff;
	min-height: 300px;
	color: #ffffff;
	font-size: 1.6em;
	z-index: 0;
}

.site-notice-box .site-notice {
	width: 1000px;
	overflow-x: hidden;
	margin: 0 auto;
	padding-top: 40px;
}

.site-notice-box .site-notice .site-notice-group {
	width: 500%;
	position: relative;
	z-index: 1;
	-webkit-transform: translate(0, 0);
	transform: translate(0, 0);
	-webkit-transition: -webkit-transform 0.5s linear;
	transition: -webkit-transform 0.5s linear;
	transition: transform 0.5s linear;
	transition: transform 0.5s linear, -webkit-transform 0.5s linear;
}

.site-notice-box .site-notice .notice-inner {
	float: left;
	width: 1000px;
}

.site-notice-box .site-notice .notice-inner h2 {
	max-width: 800px;
	padding-bottom: 10px;
	margin-bottom: 10px;
	font-size: 3em;
	border-bottom: 1px solid #ffffff;
}

.site-notice-box .site-notice .notice-inner p {
	line-height: 1.8em;
}

.site-notice-box .site-notice .notice-inner a {
	color: #ffffff;
	display: block;
	width: 100px;
	text-align: center;
	height: 30px;
	line-height: 30px;
	margin-top: 20px;
	border: 1px solid #ffffff;
}

.site-notice-box .site-notice .notice-inner a:hover {
	background-color: #ffffff;
	color: #00a8ff;
}

.site-notice-box .summary-flag {
	width: 200px;
	height: 20px;
	position: absolute;
	left: 50%;
	margin-left: -100px;
	text-align: center;
	bottom: 10px;
}

.site-notice-box .summary-flag span {
	display: inline-block;
	width: 15px;
	margin-right: 5px;
	height: 5px;
	border: 1px solid #ffffff;
	cursor: pointer;
}

.site-notice-box .summary-flag span.sf-active {
	background-color: #ffffff;
}

.site-notice-box .control-arrow {
	z-index: 0;
	width: 100%;
	max-width: 1200px;
	height: 60px;
	position: absolute;
	top: 50%;
	margin-top: -30px;
	left: 50%;
	margin-left: -600px;
}

.site-notice-box .control-arrow a {
	display: inline-block;
	width: 40px;
	height: 60px;
	cursor: pointer;
}

.site-notice-box .control-arrow a#left-arrow {
	float: left;
	background-image: url("../img/spriteSheet.png");
	background-position: -0px -40px;
}

.site-notice-box .control-arrow a#right-arrow {
	float: right;
	background-image: url("../img/spriteSheet.png");
	background-position: -40px -40px;
}

/******全站通知END******/

/******可用资源列表START*****/

section.available-ad-source {
	width: 100%;
	max-width: 1920px;
	min-width: 1200px;
	font-size: 1.6em;
	padding-top: 40px;
}

section.available-ad-source h2 {
	width: 100%;
	text-align: center;
	font-size: 3em;
	margin-bottom: 10px;
}

section.available-ad-source .c-source-tabs {
	width: 1200px;
	margin: 0 auto;
}

/******可用资源列表END*****/

